.button {
  @include font-family("Open Sans");
  color: #ffffff;
  text-transform: uppercase;
  font-size: 15px;
  letter-spacing: 1px;

  display: inline-block;
  width: 100%;
  background-color: $second-color;
  border: none;
  padding: 25px 10px;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover,
  &:focus {
    background-color: #1f1cf3;
  }

  &:active {
    opacity: 0.7;
  }
}
