.form__input {
  @include font-family("Open Sans");

  font-size: 15px;
  color: #657195;
  letter-spacing: -0.5px;

  width: 100%;
  height: 60px;
  border: 2px solid #e6e8f0;
  padding: 10px 28px;
  transition: all 0.2s ease;
  border-radius: 0;
  appearance: none;
  background-color: #fafbfe;

  &:hover,
  &:focus {
    outline: none;
    border-color: #d6d7da;
  }
}

.form__field + .form__field {
  margin-top: 20px;
}

.form__checkbox {
  margin: 20px 0 51px;
}

@media (min-width: $tablet-width) {
  .form__input {
    padding: 10px 20px;
  }

  .form__checkbox {
    margin: 33px 0 28px;
  }
}

@media (min-width: $desktop-width) {
  .form {
    display: flex;
    flex-direction: column;
  }

  .form__checkbox {
    order: 1;
    margin: 0;
  }

  .form__button {
    margin: 18px 0 34px;
  }

  .form__input {
    padding: 10px 30px;
  }
}
