.main-page__title {
  font-size: 38px;
  font-weight: 700;
  letter-spacing: -0.5px;
  text-align: center;

  margin: 0;
  padding: 54px 25px 32px;
}

@media (min-width: $tablet-width) {
  .main-page__title {
    font-size: 45px;

    padding-top: 48px;
  }
}

@media (min-width: $desktop-width) {
  .main-page__title {
    font-size: 55px;

    padding: 62px 25px 13px;
  }
}
