.ruble {
  position: relative;
  text-transform: uppercase;
  font-style: normal;

  &::before {
    content: "";
    position: absolute;
    bottom: 34%;
    left: 0;
    width: 0.5em;
    height: 0.05em;
    background-color: #ffffff;
  }

  &--grey {
    &::before {
      background-color: #8b89ff;
    }
  }
}
