.features {
  @include reset-list;
}

.features__item {
  font-size: 15px;
  letter-spacing: -0.5px;
  color: #657195;

  position: relative;
  padding-left: 25px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: $second-color;
  }
}

.features__item + .features__item {
  margin-top: 22px;
}
