.record-form {
  padding-top: 52px;
}

.record-form__block {
  position: relative;
  padding: 40px 25px;

  &--first {
    padding-top: 89px;
    padding-bottom: 41px;
    background-color: $second-color;
  }

  &--second {
    padding-bottom: 53px;
    background-color: $main-color;
    box-shadow: $shadow;
  }
}

.record-form__sale {
  color: #ffffff;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;

  position: absolute;
  top: -53px;
  left: 50%;
  transform: translateX(-50%);

  display: flex;
  flex-direction: column;
  align-items: center;
  width: 110px;
  padding: 20px 10px 10px;
  background-color: #000000;

  &::after {
    content: "";
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);

    border-left: 55px solid transparent;
    border-right: 55px solid transparent;
    border-top: 15px solid #000000;
  }
}

.record-form__sale-percent {
  font-size: 30px;
  line-height: 1;
  letter-spacing: -0.5px;

  margin: 6px 0;
}

.record-form__sale-limit {
  font-size: 15px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 400;
  letter-spacing: -0.5px;
  text-transform: none;
}

.record-form__price {
  font-size: 15px;
  color: #ffffff;
  letter-spacing: -0.5px;
  text-align: center;

  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;

  &--old {
    color: #8b89ff;
  }

  &--part {
    font-size: 18px;
    font-weight: 700;
  }
}

.record-form__price + .record-form__price {
  margin-top: 27px;
}

.record-form__price span {
  @include font-family("Roboto Slab");

  letter-spacing: -2px;
  font-weight: 300;
}

.record-form__price--old span {
  font-size: 36px;

  position: relative;
  margin-top: 5px;
  padding: 0 5px;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #8b89ff;
  }
}

.record-form__price--part span {
  font-size: 56px;
  line-height: 1;
  font-weight: 400;

  margin-bottom: 5px;
  padding-right: 13px;
}

.record-form__price--sale span {
  font-size: 40px;

  margin-bottom: 2px;
}

.record-form__title {
  font-size: 30px;
  letter-spacing: -0.5px;
  font-weight: 700;

  margin: 0 0 25px;
  padding-right: 80px;
}

.record-form__features {
  margin: 30px 0;
}

.record-form__wrapper {
  margin-bottom: 37px;
}

.record-form__info {
  color: #657195;
  letter-spacing: -0.5px;

  margin: 0;

  span {
    font-size: 20px;
    font-weight: 700;
    color: $second-color;
  }
}

.record-form__info + .record-form__info {
  margin-top: 12px;
}

@media (min-width: $tablet-width) {
  .record-form {
    padding-top: 46px;
    margin-bottom: 80px;
  }

  .record-form__sale {
    top: -20px;
  }

  .record-form__block {
    padding: 67px 123px 75px;

    &--first {
      padding-top: 133px;
      padding-bottom: 75px;
    }
  }

  .record-form__price + .record-form__price {
    margin-top: 36px;
  }

  .record-form__price--old span {
    font-size: 38px;

    margin-top: 7px;
  }

  .record-form__price--part {
    font-size: 20px;

    span {
      font-size: 80px;

      margin-bottom: 3px;
      padding: 0;
    }
  }

  .record-form__price--sale {
    font-size: 18px;

    span {
      font-size: 50px;
    }
  }

  .record-form__title {
    font-size: 38px;
    text-align: center;

    padding: 0;
    margin-bottom: 35px;
  }

  .record-form__features {
    margin: 42px 0 56px;
  }

  .record-form__wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;

    width: 345px;
    margin: 0 auto 22px;
  }

  .record-form__info {
    margin-bottom: 10px;
  }

  .record-form__info + .record-form__info {
    margin-top: 0;
  }
}

@media (min-width: $desktop-width) {
  .record-form {
    display: flex;
  }

  .record-form__block {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    width: 50%;
    padding: 58px 100px;

    &--first {
      align-items: center;
      padding-top: 110px;
      padding-bottom: 110px;
    }

    &--second {
      padding-left: 85px;
      padding-right: 130px;
    }
  }

  .record-form__title {
    font-size: 34px;
    text-align: left;

    margin: 0 0 28px;
  }

  .record-form__features {
    margin: 0 0 51px;
  }

  .record-form__wrapper {
    width: 100%;
    margin: 0 0 20px;
  }

  .record-form__price--sale {
    font-size: 20px;

    span {
      margin-bottom: 8px;
    }
  }

  .record-form__price--part {
    font-size: 23px;

    span {
      padding-left: 14px;
      margin-bottom: 0;
    }
  }

  .record-form__price + .record-form__price {
    margin-top: 30px;
  }
}
