.checkbox {
  @include font-family("Open Sans");

  font-size: 14px;
  line-height: 21px;
  color: #657195;
  letter-spacing: -0.5px;

  display: block;
  cursor: pointer;
}

.checkbox__text {
  display: block;
  padding-left: 44px;
  position: relative;
  min-height: 24px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 24px;
    height: 24px;
    border: 2px solid #e6e8f0;
    background-color: #fafbfe;
    transition: all 0.2s ease;
  }

  &:hover::before {
    border-color: #d6d7da;
  }
}

.checkbox a {
  color: $second-color;
  transition: all 0.2s ease;

  &:hover,
  &:focus {
    text-decoration: underline;
  }

  &:active {
    opacity: 0.5;
  }
}

.checkbox__input:focus ~ .checkbox__text::before {
  border-color: #d6d7da;
}

.checkbox__icon {
  display: none;
  position: absolute;
  top: 7px;
  left: 5px;
  fill: $second-color;
}

.checkbox__input:checked ~ .checkbox__text .checkbox__icon {
  display: block;
}

@media (min-width: $tablet-width) {
  .checkbox__icon {
    fill: #ffffff;
  }

  .checkbox__input:focus ~ .checkbox__text::before {
    border-color: #1f1cf3;
    background-color: #1f1cf3;
  }

  .checkbox__text {
    padding-left: 33px;

    &:hover::before {
      border-color: #1f1cf3;
      background-color: #1f1cf3;
    }

    &::before {
      border-color: $second-color;
      background-color: $second-color;
    }
  }
}

@media (min-width: $desktop-width) {
  .checkbox {
    padding-right: 20px;
  }

  .checkbox__text {
    padding-left: 40px;
  }
}
